$font-family-mono:    SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;

/* Markdown Editor
   ========================================================================== */

.md-Editor,
.md-Editor-textarea {
  width: 100%;
}

.md-Toolbar {
  /*Using block display as this is what the native toolbar widget expects*/
  /*display: flex;*/
  width: 100%;
  flex-shrink: 0;
  flex-grow: 1;
  flex-basis: 100%;
  flex-wrap: wrap;
  border: 1px solid #dfdfdf;
  padding: 8px;
  background: #fff;
}

.md-Toolbar-group {
  display: flex;
  margin-right: 16px;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: auto;

  &:last-child {
    margin-right: 0;
  }
}
/* Using native toolbar classes instead */
/*.md-Toolbar-button {*/
.md-Toolbar .a-Button {
  /*display: inline-flex;*/
  margin: 0;
  padding: 8px;
  line-height: 1;
  color: inherit;
  background-color: transparent;
  outline: none;
  border: 0;
  border-radius: 2px;
  cursor: pointer;
  transition: background-color .2s ease, box-shadow .2s ease, color .2s ease;
  align-items: center;
  box-shadow: none;

  &:hover,
  .md-Toolbar-dropdownButton:hover & {
    color: #0572CE;
    background-color: #fff;
    box-shadow: 0 0 0 1px rgba(0,0,0,.15) inset;
    box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.125), 0 2px 4px -2px rgba(0, 0, 0, 0.075);
  }

  &:active,
  &:focus,
  .md-Toolbar-dropdownButton:active &,
  .md-Toolbar-dropdownButton:focus & {
    color: #383838;
    background-color: #fff;
    box-shadow: 0 0 0 1px #0572CE inset, 0 0 0 2px #f9f9f9 inset;
  }

  &.is-active,
  &:active,
  .md-Toolbar-dropdownButton.is-active &,
  .md-Toolbar-dropdownButton:active & {
    background-color: #dedede;
    box-shadow: 0 0 0 1px rgba(0,0,0,.15) inset, 0 2px 2px rgba(0,0,0,.1) inset;
  }

  &.is-active:focus,
  .md-Toolbar-dropdownButton.is-active:focus & {
    background-color: #e6e6e6;
    box-shadow: 0 0 0 1px #0572CE inset, 0 2px 2px rgba(0,0,0,.1) inset;
  }
}
.md-Toolbar-button-icon {
  font-size: 16px;

  & + .md-Toolbar-button-label {
    margin-left: 6px;
  }
}

.md-Toolbar-button-label {
  font-size: 12px;
}

.md-Toolbar-button--iconOnly .md-Toolbar-button-label {
  display: none;
}

.md-Toolbar-dropdownButton {
    position: relative;
    display: inline-flex;
}
/*.md-Toolbar-dropdownButton:hover .md-Toolbar-dropdownButton-menu {
    display: block;
}*/
.md-Toolbar-dropdownButton-menu {
    list-style: none;
    margin: 0;
    padding: 4px 0;
    text-align: left;
    position: absolute;
    top: 100%;
    background-color: #fff;
    border: 1px solid #efefef;
    border-radius: 2px;
    overflow: hidden;
    left: 0;
    display: none;
    z-index: 3;
}
.md-Toolbar-dropdownButton-menu-item {
    background: #fff;
    padding: 8px;
    min-width: 100px;
    cursor: pointer;
}
.md-Toolbar-dropdownButton-menu-item:hover {
    background: #efefef;
}

