}

.md-Editor.md-Editor--codeEditor + .CodeMirror {
    border: 1px solid #dfdfdf;
    border-top: none;
}

#MARKDOWNIFY_EDITOR_IMAGE_SIZE .t-Form-inputContainer .checkboxradio_field {
    margin-right: 10px;
    margin-left: 5px;
}

/* Preview Styles
  =========================================================================== */

.md-Editor-previewPanel {
  padding: 16px;
  background: #fff;
  border: 1px solid rgba(0,0,0,.1);
  border-top: none;
  position: absolute;
  width: 100%;
  overflow: auto;
  /* New CodeMirror Library Scrollbar is using z-index: 6; so this is a 7 */
  z-index: 7;
}

.md-Editor-previewPanel.md-Editor-previewPanel--hide {
  height: 0px;
  padding: 0px;
  border: none;
}

/* Markdown
   ========================================================================== */

.is-markdownified {
  .CodeMirror {
    height: auto;
    margin: 16px 0;
    border: 1px solid #efefef;

    &-cursors {
      visibility: hidden !important;
    }
  }

  /* Styles for specific tags */
  a {
    word-wrap: break-word;
    hyphens: auto;
    overflow-wrap: break-word;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0 0 8px;
    font-weight: 500;
    line-height: 1.2;
    color: inherit;
  }

  p,
  pre:not(.CodeMirror-line),
  table {
    & + h1,
    & + h2,
    & + h3,
    & + h4,
    & + h5,
    & + h6 {
      margin-top: 24px;
    }
  }

  h1 {
    font-size: 40px;
    font-weight: 600;
  }

  h2 {
    font-size: 32px;
    font-weight: 600;
  }

  h3 {
    font-size: 28px;
    font-weight: 600;
  }

  h4 {
    font-size: 24px;
  }

  h5 {
    font-size: 20px;
  }

  h6 {
    font-size: 16px;
  }

  strong,
  b {
    font-weight: bold;

    & {
      font-weight: bolder;
    }
  }

  em,
  i {
    font-style: italic;
  }

  ul,
  ol {
    margin: 8px 0 16px 32px;

    li {
      font-size: inherit;
      line-height: inherit;

      &:not(:last-child) {
        margin-bottom: 4px;
      }
    }

    ul,
    ol {
      margin-top: 0;
      margin-bottom: 12px;
    }

    p {
      & + ul,
      & + ol {
        margin-top: -8px;
      }
    }
  }

  hr {
    clear: both;
    margin: 20px 0;
    background: none;
    border: 0;
    border-top: 1px solid rgba(0,0,0,.2);
  }

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }

  blockquote {
    margin: 20px;
    padding-top: 4px;
    padding-bottom: 4px;
    padding-left: 20px;
    font-size: 18px;
    line-height: 1.425;
    border-left: 4px solid rgba(0,0,0,0.1);
  }

  code {
    padding: 2px 4px;
    font-size: 1.4rem;
    font-family: $font-family-mono;
    background-color: rgba(0,0,0,.025);
    border-radius: 2px;
    box-shadow: inset rgba(0,0,0,.05) 0 0 0 1px;
  }

  pre:not(.CodeMirror-line) {
    margin-bottom: 16px;
    padding: 8px;
    font-size: 1.4rem;
    font-family: $font-family-mono;
    background-color: rgba(0,0,0,.025);
    border-radius: 4px;
    box-shadow: inset rgba(0,0,0,.05) 0 0 0 1px;
    max-width: 100%;
    overflow: auto;

    code {
      padding: 0;
      font-size: inherit;
      font-family: inherit;
      background-color: transparent;
      border-radius: 0;
      box-shadow: none;
    }
  }

  table {
    margin-top: 8px;
    margin-bottom: 20px;
//    width: 100%;
    font-size: 1.3rem;
    background-color: #fff;
    border: 1px solid rgba(0,0,0,.1);
    border-spacing: 0;
    border-collapse: separate;
    border-radius: 2px;
    box-shadow: 0 2px 4px -2px rgba(0, 0, 0, 0.075);

    &:last-child {
      margin-bottom: 8px;
    }

    th {
      font-weight: 500;
      background-color: rgba(0,0,0,.015);
    }

    th,
    td {
      padding: 6px 8px;
      border-bottom: 1px solid rgba(0,0,0,.05);
    }

    tr:hover {
      td {
        background-color: rgba(0,0,0,.0125);
      }

      th {
        background-color: rgba(0,0,0,.025);
      }
    }

    tr:last-child {
      td {
        border-bottom: 0;
      }
    }
  }
}